| DOTO:  备注
|
*/
<template>
    <div class="handoverduty">
        <div class="header">
            <el-button type="text" @click="goback()">< 交班信息</el-button>
        </div>
        <div class="outerbox">
            <div class="innerbox">
                <el-row class="bordercss">
                    <el-col :span="16">
                        <el-col :span="12">交班时间： {{detailsData.create_time}}</el-col>
                        <el-col :span="12">交班人员： {{detailsData.user_name}}</el-col>
                        <el-col :span="24" class="title">
                            <el-col :span="12">
                                <h3 class="tlc">已结账订单数（次）</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="tlc">订单实收金额（元）</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="number">{{detailsData.bill_done_pay_num}}</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="number">{{detailsData.bill_receivable_value =='.00'? '0.00' : detailsData.bill_receivable_value}}</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="tlc">未结账订单数（次）</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="tlc">未结账金额（元）</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="number">{{detailsData.bill_undone_pay_num}}</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="number">{{detailsData.bill_undone_value}}</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="tlc">现金实收（元）</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="tlc">会员储值金额（元）</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="number">{{cach_real_render()}}</h3>
                            </el-col>
                            <el-col :span="12">
                                <h3 class="number">{{vip_member_render()}}</h3>
                            </el-col>
                            <el-col :span="12">
                                <p class="number" style="font-size:14px;">(订单:￥{{detailsData.bill_cash_value}},储值:￥{{detailsData.member_cash_value}})</p>
                            </el-col>
                            <el-col :span="12">
                                <p class="number" style="font-size:14px;">(储值:￥{{detailsData.member_in_cash_value =='.00'?'0.00':detailsData.member_in_cash_value}},退款:￥ {{detailsData.member_out_cash_value =='.00'?'0.00':detailsData.member_out_cash_value}})</p>
                            </el-col>
                        </el-col>
                    </el-col>
                    <el-col :span="8">
                        <el-col :span="24" style="text-align:center;margin-bottom:10px;">---订单明细---</el-col>
                        <ul>
                            <li v-for="item in bill_list" :key="item.id">
                                <div class="left_">{{item.pay_name}}</div>
                                <div class="right_">¥  {{item.pay_amount}}</div>
                            </li>
                        </ul>
                        <el-col :span="24" style="text-align:center;margin-bottom:10px;">---会员储值---</el-col>
                        <ul>
                            <li v-for="item in member_list" :key="item.id">
                                <div class="left_">{{item.pay_name}}</div>
                                <div class="right_">¥  {{item.pay_amount}}</div>
                            </li>
                        </ul>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script >
import Vue from "vue";
import { mapState, mapMutations, mapActions } from "vuex";
import dappSDK from "dappsdk";
import {getReserveCrossDetails} from "../../../../service/handOverDuty/index.js";

export default {
    name: "index",
    data() {
        return {
            detailsData:null,
            bill_list:[],//账单
            member_list:[],//储值
        };
    },
    components: {},
    computed: {
        /*catch store.data*/
        ...mapState({
            token_: state => state.token_
        })
    },
    mounted() {
        this.getReserveCrossDetails_();
      
    },
    methods: {
        ...mapMutations(["settoken"]),

        /**
         * 1
         * @param 参数1说明 url
         * @returns 返回值的类型 void
         */
        handleClick(tab, event) {
            console.log(tab, event);
        },

        goback() {
            this.$router.push({
                path: "/handOverDuty"
            });
        },
        async getReserveCrossDetails_(){
            let param={
                token:this.token_,
                id:this.$route.query.id,
            };
            let {code,data,error} =await getReserveCrossDetails(param);
            if(code == 200){
                this.detailsData = data[0];
                this.detailsData.pay_way_sum.forEach((tiem) => {
                    if(tiem.cross_tag == '1'){
                        this.bill_list.push(tiem);
                    }
                    if(tiem.cross_tag == '2'){
                        this.member_list.push(tiem);
                    }
                });
            }
        },

         /**
         * 现金实收 = 订单金额 + 储值金额
         */
        cach_real_render(){
            return (Number(this.detailsData.member_cash_value) + Number(this.detailsData.bill_cash_value)).toFixed(2);
        },
        /**
         * 会员储值金额 = 储值金额 - 退款金额
         */
        vip_member_render(){
            return (Number(this.detailsData.member_in_cash_value) - Number(this.detailsData.member_out_cash_value)).toFixed(2);
        },
    }
};
</script>
<style>
body,
html {
    height: 100%;
}
.el-tabs__nav-wrap is-top {
    height: 64px;
}
.el-tabs__nav-scroll {
    height: 64px;
    padding-left: 40px;
}
.el-tabs__nav {
    line-height: 64px !important;
}
.el-tabs__header {
    margin: 0 !important;
}
</style>
<style lang = "less" scoped>
@import "style.less";
</style>



