/*
|--------------------------------------------------------------------------
| 组件 交班历史 path：/home/bookSpecial
|--------------------------------------------------------------------------
|
| author：薛帅
| data： vuex.userInfo
| DOTO:  备注
|
*/
<template>
    <div class="handoverduty" v-loading = "loading">
        <div class="header">
            <el-button type="text" @click="goback()">< 交班历史</el-button>
        </div>
        <div class="outerbox">
            <div class="innerbox">
                <el-row class="bordercss">
                    <el-col :span="24">营业日期：
                        <el-date-picker
                            v-model="query.date"
                            size="medium"
                            style="margin-left:12px;margin-right:12px;"
                            type="daterange"
                            range-separator="-"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            value-format="yyyy-MM-dd"
                        ></el-date-picker>交班人：
                        <el-select
                            v-model="query.user_id"
                            style="margin-left:12px;margin-right:12px;"
                            size="medium"
                            placeholder="请选择"
                        >
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                        <el-radio-group
                            @change="getReserveCrossList_()"
                            style="margin-top: -5px;"
                            v-model="query.day_num"
                            size="medium"
                        >
                            <el-radio-button label="1">今天</el-radio-button>
                            <el-radio-button label="2">昨天</el-radio-button>
                            <el-radio-button label="7">最近7天</el-radio-button>
                        </el-radio-group>
                        <el-button
                            size="medium"
                            style="margin-left: 12px;"
                            @click="getReserveCrossList_()"
                            type="primary"
                        >搜索</el-button>
                        <el-button size="medium" @click="emptyQuery()">清空</el-button>
                    </el-col>
                </el-row>
                <el-col :span="24" style="margin-top: 12px;margin-bottom: 12px;">查询到{{pagination.total}}笔记录</el-col>
                <el-row>
                    <el-table :data="tableData" border style="width: 100%">
                        <el-table-column prop="create_time" label="交班时间" align="center"></el-table-column>
                         <el-table-column prop="bill_date" label="营业日期" align="center"></el-table-column>
                        <el-table-column prop="id" label="交班人" align="center"></el-table-column>
                        <el-table-column prop="bill_done_pay_num" label="结账单数" align="center"></el-table-column>
                        <el-table-column prop="bill_receivable_value" label="应收" align="center">
                            <template slot-scope="scope">
                                ¥ {{scope.row.bill_receivable_value}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="bill_real_value" label="实收" align="center">
                             <template slot-scope="scope">
                                ¥ {{scope.row.bill_real_value}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="member_real_value" label="储值实收" align="center">
                             <template slot-scope="scope">
                                ¥ {{scope.row.member_real_value}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="member_real_gift_value" label="储值赠送" align="center">
                             <template slot-scope="scope">
                                ¥ {{scope.row.member_real_gift_value}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="user_account" label="操作账号" align="center"></el-table-column>
                        <el-table-column label="操作" align="center">
                            <template slot-scope="scope">
                                <el-button type="text" @click="print_(scope.row)">打印</el-button>
                                <el-button type="text" @click="godetails(scope.row.id)">查看详情</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-row>
                <el-row style="text-align:right;padding-top:20px;">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pagination.page"
                        :page-size="pagination.perpage"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pagination.total"
                    ></el-pagination>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script >
import Vue from "vue";
import { mapState, mapMutations, mapActions } from "vuex";
import { getReserveCrossList } from "../../../../service/handOverDuty/index.js";
import dappSDK from "dappsdk";

export default {
    name: "index",
    data() {
        return {
            tableData: [],
            query: {
                date: [],
                day_num: "1",
                user_id: ""
            },
            pagination: {
                page: 1,
                perpage: 10,
                total: 0
            },
            loading:true,
        };
    },
    components: {},
    computed: {
        /*catch store.data*/
        ...mapState({
            token_: state => state.token_
        })
    },
    mounted() {
        this.getReserveCrossList_();
        //默认时间昨天到今天
        let today = new Date().getFullYear() +'-'+new Date().getMonth()+1+'-'+new Date().getDate();
        let yestoday1 = new Date(new Date().getTime() - 3600 * 1000 * 24);
        let yestoday = yestoday1.getFullYear() +'-'+yestoday1.getMonth()+'-'+yestoday1.getDate();;
        this.query.date=[yestoday,today]

    },
    methods: {
        ...mapMutations(["settoken"]),

        async getReserveCrossList_() {
            this.loading = true;
            let param = {
                token: this.token_,
                start_date: this.query.date[0] ? this.query.date[0] : "",
                end_date: this.query.date[1] ? this.query.date[1] : "",
                day_num: this.query.day_num,
                user_id: this.query.user_id || "123",
                page: this.pagination.page,
                perpage: this.pagination.perpage
            };
            let { code, data, error } = await getReserveCrossList(param);
            if (code == 200) {
                this.tableData = data.data;
                this.pagination.total = data.total;
            }
            this.loading = false;
        },
        emptyQuery(){
            this.query= {
                date: [],
                day_num: "1",
                user_id: ""
            };
        },
        // 打印
        print_(rowdata){
            dappSDK.exec("system", "handDutyHistroyListPrint", rowdata, function(err, data) {
                if (err) {
                    var code = error.code;
                    if (code == "-10001") {
                        console.log(
                            "namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本"
                        );
                    }
                    if (code == "-10002") {
                        console.log(
                            "function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本"
                        );
                    }
                    if (code == "-10003") {
                        console.log("params错误");
                    }
                    if (code == "-10004") {
                        console.log("params错误");
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert("Error: " + err.message);
                }
            });
        },
        /**
         * 1
         * @param 参数1说明 url
         * @returns 返回值的类型 void
         */
        handleClick(tab, event) {
            console.log(tab, event);
        },

        goback() {
            this.$router.push({
                path: "/handOverDuty"
            });
        },

        godetails(id) {
            this.$router.push({
                path: "/handOverDutyHistorydetails",
                query: { id: id }
            });
        }
    }
};
</script>
<style>
body,
html {
    height: 100%;
}
.el-tabs__nav-wrap is-top {
    height: 64px;
}
.el-tabs__nav-scroll {
    height: 64px;
    padding-left: 40px;
}
.el-tabs__nav {
    line-height: 64px !important;
}
.el-tabs__header {
    margin: 0 !important;
}
</style>
<style lang = "less" scoped>
@import "style.less";
</style>



