<script >
import Vue from "vue";
import { mapState, mapMutations, mapActions } from "vuex";
import {getCashierinfo} from '../../../service/handOverDuty/index.js'
import dappSDK from "dappsdk";

export default {
    name: "index",
    data() {
        return {
            activetab: "handoverduty",
            handOverDutyData:null,
            loading:false,
            currenttime:'',
            setInterval_:null,
        };
    },
    components: {},
    computed: {
        /*catch store.data*/
        ...mapState({
            token_: state => state.token_
        })
    },
    mounted() {
        window.rePage = this.rePage;
        // this.rePage('order')
        //拿到token与
        // this.Initialization_();
        this.getCashierinfo_();
        this.getCurrentTime();
       this.setInterval_= setInterval(()=>{
            this.getCurrentTime()
       },1000)
    },
    methods: {
        ...mapMutations(["settoken"]),

        /**
         * 1
         * @param 参数1说明 url
         * @returns 返回值的类型 void
         */
        handleClick(tab, event) {
            console.log(tab, event);
        },

        gohandoverduty() {
            this.$router.push({
                path: "/handOverDutystart"
            });
        },
        gostartDayOver() {
            this.$router.push({
                path: "/startDayOver"
            });
        },
        goHistory(){
          this.$router.push({
                path: "/handOverDutyHistoryList"
            });
        },

        async getCashierinfo_(){
            this.loading = true;
            let param={
                token:this.token_
            };  
            let {code,data,message} = await getCashierinfo(param);
            if(code == 200){
                this.handOverDutyData = data;
            }
            this.loading = false;
        },

        getCurrentTime(){
            var today = new Date();
	        var year = today.getFullYear();
	        var month = today.getMonth()+1;
	        var date = today.getDate();
	        var day = today.getDay();
	        var hours = today.getHours()< 10 ? '0'+today.getHours() : today.getHours();
	        var minutes	= today.getMinutes()< 10 ? '0'+today.getMinutes() : today.getMinutes();
	        var seconds	= today.getSeconds()< 10 ? '0'+today.getSeconds() : today.getSeconds();
            var milliseconds = today.getMilliseconds() < 10 ? '0'+today.getMilliseconds() : today.getMilliseconds();
            this.currenttime =  year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds;
        },
    }
};
</script>
