<template>
    <div v-loading="true" style="height:100%;display: contents;">{{token_}}</div>
</template>

<script >
import Vue from "vue";
import { mapState, mapMutations, mapActions } from "vuex";
import dappSDK from "dappsdk";

export default {
    name: "index",
    data() {
        return {};
    },
    components: {},
    computed: {
        /*catch store.data*/
        ...mapState({
            token_: state => state.token_
        })
    },
    mounted() {
        window.rePage = this.rePage;
        // this.rePage('tablePayNumberOrderDetails')
        //拿到token与
        this.Initialization_();
    },
    methods: {
        ...mapMutations(["settoken"]),
        /**
         * 与android约定好的方法
         * @param 参数1说明 命名空间
         * @param 参数2说明 函数名.
         * @param 参数3说明 调用该函数时的参数.
         * @param 参数2说明 callback
         * @returns 返回值的类型 {}
         */
        Initialization_() {
            const that = this;
            dappSDK.exec("system", "initWebview", {}, function(err, data) {
                if (err) {
                    var code = error.code;
                    if (code == "-10001") {
                        console.log(
                            "namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本"
                        );
                    }
                    if (code == "-10002") {
                        console.log(
                            "function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本"
                        );
                    }
                    if (code == "-10003") {
                        console.log("params错误");
                    }
                    if (code == "-10004") {
                        console.log("params错误");
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert("Error: " + err.message);
                }

                that.settoken(data.token); //设置token
                that.rePage(data.page);
                console.log(
                    data,
                    "webview system initWebview console message~~~~~~~"
                );
            });
        },

        /**
         * 与android约定好的方法
         * @param 参数1说明 命名空间
         * @param 参数2说明 函数名.
         * @param 参数3说明 调用该函数时的参数.
         * @param 参数2说明 callback
         * @returns 返回值的类型 {}
         */
        currentPage(pagename) {
            const that = this;
            let params={
                'page' : pagename
            }
            dappSDK.exec("system", "currentPage", params, function(err, data) {
                if (err) {
                    var code = error.code;
                    if (code == "-10001") {
                        console.log(
                            "namespace错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本"
                        );
                    }
                    if (code == "-10002") {
                        console.log(
                            "function错误, 可能是sdk与native端的版本不对应, 尝试升级app和sdk到最新版本"
                        );
                    }
                    if (code == "-10003") {
                        console.log("params错误");
                    }
                    if (code == "-10004") {
                        console.log("params错误");
                    }
                    console.log(code);
                    // 如果错误存在, 那么return可以保证之后的代码不执行
                    return alert("Error: " + err.message);
                }
            });
        },

        /**
         * 初始化webview跳转页面func
         * @param 参数1说明 url
         * @returns 返回值的类型 void
         */
        rePage(url) {
            const that = this;
            switch (url) {
                case "order":
                    that.$router.push({
                        path: "/orderList"
                    });
                    that.currentPage('order')
                    break;
                case "handOverDuty":
                    that.$router.push({
                        path: "/handOverDuty"
                    });
                    that.currentPage('handOverDuty')
                break;
                case "tablePayNumberOrderDetails":
                     that.$router.push({
                        path: "/tableDetailInfo"
                    });
                    that.currentPage('tablePayNumberOrderDetails')
                break;
                default:
                    that.$router.push({
                        path: "/orderList"
                    });
                    that.currentPage('order')
                    break;
            }
        }
    }
};
</script>



