import {get,post} from './../axiosConfig'
import reHttp from './../http'


/******************** 交班 ********************/

/**
 * 查询交班列表
 */
export function getReserveCrossList (param) {
    return post(`${reHttp}/frontend/cross/list`,param)
}
/**
 * 查询交班详情
 */
export function getReserveCrossDetails (param) {
    return post(`${reHttp}/frontend/cross/details`,param)
}

/**
 * 交班主页
 */
export function getCashierCross (param) {
    return post(`${reHttp}/frontend/cross/cashierCross`,param)
}

/**
 * 交班提交
 */
export function setCross (param) {
    return post(`${reHttp}/frontend/cross/setCross`,param)
}

/**
 * 交班收银员信息
 */
export function getCashierinfo (param) {
    return post(`${reHttp}/frontend/cross/cashierinfo`,param)
}







export default {
    getReserveCrossList,
    getReserveCrossDetails,
    getCashierCross,
    setCross,
    getCashierinfo
}


